<template>
	<view class="content">
		<!-- <image class="logo" src="/static/logo.png"></image> -->
		
		
	<!-- 	<view class="box2" hover-class="boxHover">
			<view class="inner" hover-class="innerHover" hover-stop-propagation></view>
		</view> -->
		<van-tabs v-model:active="activeName" type='line' line-height='3px' line-width='30px'color='blue' animated> 
		  <van-tab title="校内" name="a">
			  <view>
				  <scroll-view scroll-x class='navscroll'>
					  <view class='item' v-for='item in 10'>
						  官方
					  </view>
					  
					 
				  </scroll-view>
				  
				  
				  <view class='content'>
					  <view class='row' v-for="(item, index) in listArr" :key="item._id">
						  <newsbox @click.native="gotoDetail(item)" :typeLabel="item.hType"  :title="item.head" :content='item.detail' :seqNum="index+1" :pic='item.picurls'></newsbox>
					  </view>
				  </view>
			  </view>
		  </van-tab>
		  
		  
		  
		  
		  
		  
		  
		  
		  <van-tab title="校外" name="b">
			<scroll-view scroll-x class='navscroll'>
								  <view class='item' v-for='item in 10'>
									  官方
								  </view>
								  
								 
			</scroll-view>
			<view class='content'>
								  <view class='row' v-for="item in 10">
									  <newsbox @click.native="gotoDetail"></newsbox>
								  </view>
			</view>
			  
			  
		</van-tab>
		  
		</van-tabs>


	</view>

</template>

<script>
	export default {
		data() {
			return {
				activeName:'',
				listArr:[],
				title: 'Hello',
				type:['samepeople','unused','topic'],
				head:['寻找一起冲浪的小伙伴','用了四年的充电宝','有没有推荐的强化学习视频'],
			}
		},
		onLoad() {
			this.getData()
			console.log('dataget',this.listArr)
		},
		methods: {
			//获取数据库的列表
			getData(){
				uniCloud.callFunction({
					name:"art_get_all"
				}).then(res =>{
					console.log(res)
					this.listArr = res.result.data 
				})
			},
			
			
			gotoDetail(item) {
			  let targetPage = '';
			
			  if (item.hType === 'dz') {
			    targetPage = '/pages/employDetail/employDetail';
			  } else if (item.hType === 'xz') {
			    targetPage = '/pages/esDetail/esDetail';
			  } else if (item.hType === 'tz') {
			    targetPage = '/pages/tbDetail/tbDetail';
			  } else {
			    targetPage = '/pages/detail/detail';
			  }
			
			  // 使用 eventChannel 传递 head 和 detail 数据
			  uni.navigateTo({
			    url: targetPage,
			    success: function (res) {
			      res.eventChannel.emit('sendData', { head: item.head, detail: item.detail });
			    }
			  });
			},

		}
	}
</script>

<style lang='scss' scoped>
.navscroll{
	height: 100rpx;
	background:#F7F8FA;
	white-space: nowrap;
	.item{
		font-size: 40rpx;
		display: inline-block;
		line-height: 100rpx;
		padding:0 30rpx;
		
		
	}
}

.content{
	padding:30rpx;
	.row{
		border-bottom: 1px dotted #efefef;
		padding:15rpx 0;
		
	}
}

</style>
